.title {
  background-color: rgb(24, 24, 24);
  box-shadow: 0 0 10px 10px rgb(70, 70, 70);
}

h1,
.resultText {
  color: rgb(220, 220, 220);
}

.resultAttribute {
  color: #a73335;
}

.card-header .fa {
  transition: 0.3s transform ease-in-out;
}
.card-header .collapsed .fa {
  transform: rotate(-90deg);
}

.modifier-heading {
  background-color: rgb(44, 48, 52) !important;
}

.modifier-body {
  background-color: rgb(50, 54, 58) !important;
}

hr {
  border: none;
  border-top: 3px double red;
  color: red;
  overflow: visible;
  text-align: center;
  height: 5px;
}

hr.general:after {
  content: "General";
}

hr.appearance:after {
  content: "Appearance";
}

hr.traits:after {
  content: "Traits";
}

hr.voice:after {
  content: "Voice";
}

hr.statBlock:after {
  content: "Stat Block";
}

hr:after {
  background: rgb(33, 33, 33);

  color: rgb(255, 255, 255);
  padding: 0 4px;
  position: relative;
  top: -13px;
}
